import * as Yup from 'yup';

export const validPasswordScheme = Yup.object().shape({
  password: Yup.string()
    .required('Field is required')
    .min(8, '     8 ')
    .max(32, '    32 ')
    .matches(
      /^(?=.*[A-Z])(?=.*\d).{8,16}$/,
      '    8  ,   ,  '
    ),
  newPassword: Yup.string()
    .required('Field is required')
    .min(8, '     8 ')
    .max(32, '    32 ')
    .matches(
      /^(?=.*[A-Z])(?=.*\d).{8,16}$/,
      '    8  ,   ,  '
    ),
});
